import type { InjectionKey, Ref } from 'vue';

/**
 * 表单组件数据模型key
 */
export const DATA_FORM_MODEL_KEY = Symbol('DATA_FORM_MODEL_KEY') as InjectionKey<
  Ref<{
    [key: string]: boolean;
  }>
>;

/**
 * 密文表单项加解密状态key
 */
export const CIPHERTEXT_STATUS_KEY = Symbol('CIPHERTEXT_STATUS_KEY') as InjectionKey<
  Ref<{
    [key: string]: boolean;
  }>
>;

/**
 * 表单组件地图实例key
 */
export const DATA_FORM_AMAP_KEY = Symbol('DATA_FORM_AMAP_KEY') as InjectionKey<
  Ref<{
    [key: string]: any;
  }>
>;
